.main {
  width: 368px;
  margin: 0 auto;
}
.main .ant-form-item {
  margin-bottom: 24px;
}
.main h3 {
  margin-bottom: 20px;
  font-size: 16px;
}
.main .getCaptcha {
  display: block;
  width: 100%;
}
.main .submit {
  width: 50%;
}
.main .login {
  float: right;
}
.success,
.warning,
.error {
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.success {
  color: #52c41a;
}
.warning {
  color: #faad14;
}
.error {
  color: #f5222d;
}
